import Swal from "sweetalert2";
export const validatephone = (e) => {
  const value = e;
  console.log(value, "value");
  const reg = /^1([0-9]*)?$/; // 1
  if (reg.test(value) && 1 <= value.length <= 11) {
    return true;
  } else {
    return false;
  }
};
export const showDialog = (msg, type, functions) => {
  Swal.fire({
    text: msg,
    icon: type,
    confirmButtonText: localStorage.getItem('language') === 'zh-cn' ? '' : 'sure',
    customClass: {
      icon: type === 'error' ? "del-icon" : 'suc-icon',
    },
    //  "success",
    // reverseButtons: true // 
  }).then((isConfirm) => {
    try {
      //   
      if (isConfirm.value) {
        functions(isConfirm.value);
        // Swal.fire("", "", "success");
      } else {
        functions(isConfirm.value);
        // Swal.fire("", "", "error");
      }
    } catch (e) {
      return "error";
    }
  });
};
// 
export const debounce = (fn, ms) => {  //fn: ms:
  let timerId
  return function () {
    timerId && clearTimeout(timerId)
  
    timerId = setTimeout(() => {
      fn.apply(this, arguments)
    }, ms)
  }
}
/**
* 
* 33~47!~/
* 48~570~9
* 58~64:~@
* 65~90A~Z
* 91~96[~`
* 97~122a~z
* 123~127{~
* @param length   length + 2
* @param hasNum  1- 0-
* @param hasChar  1- 0-
* @param hasSymbol  1- 0-
* @param caseSense  1- 0-
* @param lowerCase hasChar0caseSense1 1- 0-
*/
export function genEnCode (length, hasNum, hasChar, hasSymbol, caseSense, lowerCase) {
  var m = ''
  if (hasNum === 0 && hasChar === 0 && hasSymbol === 0) return m
  for (var i = length; i >= 0; i--) {
    var num = Math.floor((Math.random() * 94) + 33)
    if (
      (
        (hasNum === 0) && ((num >= 48) && (num <= 57))
      ) || (
        (hasChar === 0) && ((
          (num >= 65) && (num <= 90)
        ) || (
            (num >= 97) && (num <= 122)
          ))
      ) || (
        (hasSymbol === 0) && ((
          (num >= 33) && (num <= 47)
        ) || (
            (num >= 58) && (num <= 64)
          ) || (
            (num >= 91) && (num <= 96)
          ) || (
            (num >= 123) && (num <= 127)
          ))
      )
    ) {
      i++
      continue
    }
    m += String.fromCharCode(num)
  }
  if (caseSense === '0') {
    m = (lowerCase === '0') ? m.toUpperCase() : m.toLowerCase()
  }
  return m
}
