import React, { useEffect, useState, useRef } from "react";
import "./register.less";
import { Input, Select, Button, Modal, Checkbox, Tabs } from "antd";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { RedoOutlined, WarningTwoTone } from "@ant-design/icons";
import { validatephone, showDialog } from "../../utils/util";
import {
  captcha,
  putcaptcha,
  register,
  phoneCode,
  emailCode,
  getCommon,
  country,
} from "../../api/common";
import { data } from "browserslist";
import $ from 'jquery';
const { Option } = Select;

function Register() {

  window.captchaCheckCancel = () => {
    // 
    setIsModalVisible(false)
  }
  //  
  window.captchaCheckSuccsss = (bol, captcha, token) => {
    if (bol) {
      localStorage.setItem("jwt", token);
      setincaptcha(captcha);
      setphoneCode(captcha)
      setIsModalVisible(false)
    }
  }

  // 
  const [captchaHtml, setCaptchaHtml] = useState(null)

  const { TabPane } = Tabs;
  const [flag, setFlag] = useState(true);
  let [count, setCountDown] = useState(60);
  const [errortext, seterrortext] = useState("");
  const [forms, validateform] = useState({
    username: "",
    phone: "",
    code: "",
    password: "",
    passwordtwice: "",
  });
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [imgsrc, setimgsrc] = useState("");
  const [incaptcha, setincaptcha] = useState("");
  const [caperrortext, setcaperrortext] = useState("");
  const [spin, setspin] = useState(false);
  const [com, setcom] = useState("");
  const [checked, setchecked] = useState(false);
  const [countrycode, setcountry] = useState(86);
  const [options, setoptions] = useState([]);
  const [activeKey, setactiveKey] = useState("1");

  useEffect(() => {
    getCommons();
    getcountry();
  }, []);
  const getcountry = () => {
    country({ keywords: "" }).then((res) => {
      if (res.data.status === 200) {
        setoptions(res.data.data.list);
      }
    });
  };
  const getCommons = () => {
    getCommon().then((res) => {
      if (res.data.status === 200) {
        setcom(res.data.data);
      }
    });
  };
  const onChangechecked = (e) => {
    setchecked(e.target.checked);
    if (e.target.checked) {
      seterrortext("");
    }
    console.log(`checked = ${e.target.checked}`);
  };
  const selectcountry = (e) => {
    setcountry(e);
    localStorage.setItem("countrycode", e);
  };
  const navigate = useNavigate();
  const tologin = () => {
    navigate("/login");
  };
  const handleCancel = () => {
    setincaptcha("");
    setIsModalVisible(false);
    console.log(incaptcha, "handleCancel");
  };
  const checkcaptcha = () => {
    console.log(incaptcha, "incaptcha");
    if (incaptcha) {
      putcaptcha({
        captcha: incaptcha,
        token: localStorage.getItem("jwt"),
      }).then((res) => {
        if (res.data.status != 200) {
          setcaperrortext(res.data.msg);
        } else {
          setcaperrortext("");
          setIsModalVisible(false);
          setphoneCode(incaptcha);
        }
        setincaptcha("");
        console.log(res, "res");
      });
    } else {
      setcaperrortext("");
    }
  };
  //
  const setphoneCode = (captcha) => {
    let params = {
      action: "register",
      phone_code: countrycode,
      phone: activeKey === "2" ? forms.phone : "",
      email: activeKey === "1" ? forms.username : "",
      captcha,
      token: localStorage.getItem("jwt"),
    };
    if (activeKey === "2") {
      delete params.mail;
      phoneCode(params).then((res) => {
        if (res.data.status === 200) {
          countDown();
        }
      });
    } else {
      delete params.phone;
      emailCode(params).then((res) => {
        if (res.data.status === 200) {
          countDown();
        }
      });
    }
  };

  const setcode = () => {
    if (activeKey === "1") {
      if (!forms.username) {
        seterrortext("");
        return;
      }
      if (
        forms.username.search(
          /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/
        ) === -1
      ) {
        seterrortext("");
        return;
      }
    } else {
      if (!forms.phone) {
        seterrortext("");
        return;
      }
      if (!validatephone(forms.phone)) {
        seterrortext("");
        return;
      }
    }
    seterrortext("");
    if (Number(com.captcha_client_register) === 1) {
      setIsModalVisible(true);
      getcaptchadata();
    } else {
      setphoneCode(incaptcha);
    }
    setincaptcha("");
    setcaperrortext("");
    console.log(incaptcha, "");
  };
  //
  const getcaptchadata = () => {
    captcha().then((res) => {
      setspin(false);
      if (res.data.status === 200) {
        setCaptchaHtml(res.data.data.html)
        $("#captcha").html(res.data.data.html)
        // setimgsrc(res.data.data.captcha);
        // localStorage.setItem("jwt", res.data.data.token);
      }

      // setspin(false);
      // if (res.data.status === 200) {
      //   setimgsrc(res.data.data.captcha);
      //   localStorage.setItem("jwt", res.data.data.token);
      // }
      // console.log(res, "res");
    });
  };
  const getcaptcha = () => {
    setspin(true);
    getcaptchadata();
  };
  const changecaptcha = (e) => {
    setincaptcha(e.target.value);
    console.log(incaptcha, e.target.value, "changecaptcha");
  };

  //
  let timer = null;
  const countDown = () => {
    setFlag(false);
    setCountDown(--count);
    timer = setInterval(() => {
      if (count > 1) {
        setFlag(false);
        setCountDown(--count);
      } else {
        clearInterval(timer);
        timer = null;
        setFlag(true);
        setCountDown(60);
      }
    }, 1000);
  };

  const changeuername = (e) => {
    validateform({
      ...forms,
      username: e.target.value,
    });
  };
  const getphonevalue = (e) => {
    validateform({
      ...forms,
      phone: e.target.value,
    });
  };
  const ChangecodeValue = (e) => {
    validateform({
      ...forms,
      code: e.target.value,
    });
  };
  const changepassward = (e) => {
    validateform({
      ...forms,
      password: e.target.value,
    });
  };
  const changepasswardtwice = (e) => {
    validateform({
      ...forms,
      passwordtwice: e.target.value,
    });
  };

  //
  const submit = () => {
    getCommons();
    if (activeKey === "1") {
      if (!forms.username) {
        seterrortext("");
        return;
      }
      if (
        forms.username.search(
          /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/
        ) === -1
      ) {
        seterrortext("");
        return;
      }
    } else {
      if (!forms.phone) {
        seterrortext("");
        return;
      }

      if (!validatephone(forms.phone)) {
        seterrortext("");
        return;
      }
    }
    console.log(forms, "formstttt");
    if (!forms.code) {
      seterrortext("");
      return;
    }
    if (!forms.password) {
      seterrortext("");
      return;
    } else if (forms.password.length < 6 || forms.password.length > 32) {
      seterrortext("6-32");
      return;
    }
    if (!forms.passwordtwice) {
      seterrortext("");
      return;
    } else if (forms.passwordtwice !== forms.password) {
      seterrortext("");
      return;
    }
    if (!checked) {
      seterrortext("");
      return;
    }
    toregister();
  };
  const toregister = () => {
    console.log(1111);

    seterrortext("");
    let params = {
      type: activeKey === "2" ? "phone" : "email",
      account: activeKey === "1" ? forms.username : forms.phone,
      phone_code: countrycode,
      code: forms.code,
      username: "",
      password: forms.password,
      re_password: forms.passwordtwice,
      captcha: incaptcha,
      token: localStorage.getItem("jwt"),
    };

    register(params).then((res) => {
      if (res.data.status === 200) {
        showDialog("", "success");
        navigate("/account");
        localStorage.setItem("jwt", res.data.data.jwt);
      }
    });
  };
  const onChangeregister = (e) => {
    setactiveKey(e);
    seterrortext("");
    console.log(e, "eee");
  };
  return (
    <div className="login">
      <div className="l-bg"></div>
      <div className="r-bg"></div>
      <div className="r-bg-color"></div>
      <div className="form register_from">
        <div className="login_title"></div>
        <div className="login_subtitle">
          <span></span>
          <span className="toregister" onClick={tologin}>
            
          </span>
        </div>
        <Tabs defaultActiveKey="1" centered="true" onChange={onChangeregister}>
          <TabPane tab="" key="1">
            <div className="form_item username">
              <Input
                style={{
                  width: "100%",
                  backgroundColor: "#f3f3f9",
                }}
                bordered={false}
                placeholder=""
                defaultValue=""
                onChange={changeuername}
              />
            </div>
          </TabPane>
          <TabPane tab="" key="2">
            <div className="form_item select_input">
              <Select
                defaultValue=" +86"
                className="item_select"
                onChange={selectcountry}
              >
                {options.map((item, index) => {
                  return (
                    <Option key={index} value={item.phone_code}>
                      {item.name_zh}+{item.phone_code}
                    </Option>
                  );
                })}
              </Select>
              <Input
                style={{
                  width: "100%",
                  backgroundColor: "#f3f3f9",
                }}
                bordered={false}
                onChange={getphonevalue}
              />
            </div>
          </TabPane>
        </Tabs>
        <div className="code_item register_code">
          <div className="form_item_code ">
            <Input
              style={{
                width: "100%",
                backgroundColor: "#f3f3f9",
              }}
              bordered={false}
              placeholder=""
              defaultValue=""
              onChange={ChangecodeValue}
            />
          </div>
          {flag ? (
            <Button className="bottom_code" onClick={setcode}>
              
            </Button>
          ) : (
            <div className="count_down">{count} </div>
          )}
        </div>
        <div className="form_item form_item_passward">
          <Input
            style={{
              width: "100%",
              backgroundColor: "#f3f3f9",
            }}
            bordered={false}
            placeholder="6-32"
            type="password"
            defaultValue=""
            onChange={changepassward}
          />
        </div>
        <div className="form_item form_item_passward passward_twice">
          <Input
            style={{
              width: "100%",
              backgroundColor: "#f3f3f9",
            }}
            bordered={false}
            placeholder=""
            defaultValue=""
            type="password"
            onChange={changepasswardtwice}
          />
        </div>
        <div className="setcodebox register_errortext">
          {errortext ? (
            <div className="flex just_between">
              <div className="errortip">
                <WarningTwoTone twoToneColor="#f64e60" />
                {errortext}
              </div>
            </div>
          ) : (
            ""
          )}
          <div className="agreement">
            <Checkbox onChange={onChangechecked}>
              
              <a className="agreement_link" href={com.terms_service_url}>
                
              </a>
            </Checkbox>
          </div>
          <div
            className="login_btn register_btn phonelogin_btn"
            onClick={submit}
          >
            
          </div>
        </div>
      </div>



      {
        isModalVisible &&
        <div id="captcha" >

        </div>
      }

      {/* <Modal
        title=""
        visible={isModalVisible}
        className="picCheck"
        onCancel={handleCancel}
        footer={[]}
      >
        <div className="captchabox flex just_between">
          <Input
            style={{
              width: "70%",
              border: "none",
              outline: "none",
              backgroundColor: "#f3f3f9",
              height: "40px",
            }}
            bordered={false}
            onChange={changecaptcha}
            defaultValue={incaptcha}
            value={incaptcha}
          />
          <img src={imgsrc} alt="" className="captcha" />
          <RedoOutlined
            rotate={290}
            // spin={spin}
            className="outlined"
            onClick={getcaptcha}
          />
          <div className="secaptchabox">
            {caperrortext ? (
              <div className="flex just_between">
                <div className="errortip">
                  <WarningTwoTone twoToneColor="#f64e60" />
                  {caperrortext}
                </div>
              </div>
            ) : (
              ""
            )}
          </div>
        </div>
        <div className="check_btn" onClick={checkcaptcha}>
          
        </div>
      </Modal> */}
    </div>
  );
}
// 
export default connect((state) => ({}))(Register);
