import React from "react";
import { NavLink } from "react-router-dom";
import {
  Layout,
  Menu,
  Input,
  Button,
  Dropdown,
  Badge,
  Popover,
  message
} from "antd";
import { useTranslation } from "react-i18next";
import {
  MenuUnfoldOutlined,
  SearchOutlined,
  DownOutlined,
} from "@ant-design/icons";
import { useNavigate } from "react-router-dom";
import "./TopHeader.css";
import { logout } from '@/api/common.js';
import Swal from "sweetalert2";
export default function TopHeader (props) {
  const navigate = useNavigate();
  const { i18n, t } = useTranslation();
  const { Header } = Layout;
  const changeState = () => {
    props.changeState();
  };
  const handleMenuClick = (e) => {
    i18n.changeLanguage(e.key);
    localStorage.setItem("language", e.key);
    if (e.key === 'zh-cn') {
      localStorage.setItem("country_url", 'CN')
    } else {
      localStorage.setItem("country_url", 'US')
    }
    window.location.reload();
  };
  // 
  const showDialog = (msg) => {
    Swal.fire({
      title: msg,
      icon: "success",
      confirmButtonText: t("common.sure"),
    }).then(() => {
      navigate("/login", { replace: true });
    });
  };
  // 
  const handelLayout = async () => {
    try {
      const res = await logout()
      localStorage.removeItem('jwt')
      showDialog("");
      setTimeout(() => {
        navigate("/login", { replace: true });
        Swal.close();
      }, 1000)
    } catch (error) {
      message.error(error.data.msg);
    }
  }
  /*  */
  const languageMenu = (
    <Menu
      onClick={handleMenuClick}
      items={[
        {
          label: (
            <>
              <img src={require('@/assets/images/cloud/CN.png')} alt="" className="select-img" />
              <span></span>
            </>
          ),
          disabled: localStorage.getItem('language') === 'zh-cn',
          key: "zh-cn",
        },
        {
          label: (
            <>
              <img src={require('@/assets/images/cloud/US.png')} alt="" className="select-img" />
              <span>English</span>
            </>
          ),
          disabled: localStorage.getItem('language') === 'en-us',
          key: "en-us",
        },
      ]}
    />
  );
  /*  */
  const proMenu = (
    <Menu
      items={[
        {
          key: "add_cloud",
          label: <NavLink to="/add_cloud">{t("menu.cloud")}</NavLink>,
        },
        {
          key: "network",
          label: <NavLink to="/network">{t("cloud.network")}</NavLink>,
        },
        {
          key: "storage",
          label: (
            <NavLink to="/storage">
              {t("menu.storage")}
            </NavLink>
          ),
        },
        // {
        //   key: "monitor",
        //   label: <NavLink to="/monitor">{t("menu.monitor")}</NavLink>,
        // },
        {
          key: "image",
          label: <NavLink to="/image">{t("menu.image")}</NavLink>,
        },
      ]}
    />
  );
  /*  */
  // const content = (
  //   <div className="user-info">
  //     <div className="top">
  //       <Image src="" className="img com-radius"></Image>
  //       <div className="text">
  //         <p className="name"></p>
  //         <p className="price">421/</p>
  //       </div>
  //     </div>
  //     <div className="info">
  //       <div className="item clear">
  //         <p className="l-label left"></p>
  //         <p className="r-txt right">@fifestudios.com</p>
  //       </div>
  //       <div className="item clear">
  //         <p className="l-label left"></p>
  //         <p className="r-txt right">43(16)98462644</p>
  //       </div>
  //       <div className="item clear">
  //         <p className="l-label left"></p>
  //         <p className="r-txt right"></p>
  //       </div>
  //     </div>
  //     <button className="logout com-radius com-hover" onClick={outlogin}>
  //       
  //     </button>
  //   </div>
  // );

  return (
    <>
      <Header className="com-header">
        <div className="box">
          <MenuUnfoldOutlined onClick={changeState} className="menuIcon" />
          <Input
            placeholder={t("input")}
            prefix={<SearchOutlined />}
            className="t-input"
          />
        </div>
        <div className="box">
          <div className="item" id="product">
            <Dropdown
              overlay={proMenu}
              placement="bottomRight"
              getPopupContainer={() => document.getElementById("product")}
              trigger={["click"]}
            >
              <Button className="com-radius t-product">
                {t("menu.product")}
                <DownOutlined />
              </Button>
            </Dropdown>
          </div>
          <div className="item">
            <Badge count={6}>
              <div className="bg">
                <img src={require(`@/assets/images/mes.png`)} alt="" />
              </div>
            </Badge>
          </div>
          {/* <div className="item" id="avatar">
            <Popover
              placement="topRight"
              content={content}
              getPopupContainer={() => document.getElementById("avatar")}
              trigger="click"
            >
              <div className="bg">
                <Avatar
                  src="https://joeschmoe.io/api/v1/random"
                  className="avatar"
                />
              </div>
            </Popover>
          </div> */}
          <div className="item" id="language">
            <Dropdown
              overlay={languageMenu}
              getPopupContainer={() => document.getElementById("language")}
              trigger="hover"
            >
              <div className="bg">
                <img src={localStorage.getItem('country_url') ? require(`@/assets/images/cloud/${localStorage.getItem('country_url')}.png`) : require(`@/assets/images/CN.png`)} alt="" />
              </div>
            </Dropdown>
          </div>
          <div className="item" onClick={handelLayout}>
            <Popover
              content={t("common.layout")}
              trigger="hover"
            >
              <div className="bg">
                <img src={require('@/assets/images/layout.png')} alt="" />
              </div>
            </Popover>

          </div>
        </div>
      </Header>
      <div className="empty"></div>
    </>
  );
}
