import React, { useState, useEffect } from 'react'
import { Layout } from 'antd'
import './Layout.css'
import SideMenu from '@/components/sideMenu/SideMenu'
import TopHeader from '@/components/topHeader/TopHeader'
import { CenterRouter } from '@/router/routes'
import { clearPending } from '@/utils/request'
import { getCommon } from "@/api/common.js"
const { Content } = Layout;


export default function App () {
  const [collapsed, setCollapsed] = useState(false)
  const [wh] = useState(window.innerWidth)
  function handleResize (e) {
    if (e.target.innerWidth < 992) {
      setCollapsed(true)
    } else {
      setCollapsed(false)
    }
  }
  function hideMenu () {
    setCollapsed(!collapsed)
  }
  useEffect(() => {
    const lang = localStorage.getItem('language')
    getCommonConfig()
    if (!lang) {
      localStorage.setItem('language', 'zh-cn')
    }
    if (wh < 992) {
      setCollapsed(true)
    }
    window.addEventListener('resize', handleResize)
    clearPending()
    return () => {
      window.removeEventListener('resize', handleResize)
    }
  }, [wh])
  // 
  const getCommonConfig = async () => {
    try {
      const res = await getCommon()
      localStorage.setItem('font_config', JSON.stringify(res.data.data))
    } catch (error) {
    }
  }
  function changeState () {
    setCollapsed(!collapsed)
  }
  return (
    <Layout className='com-box'>
      <SideMenu collapsed={collapsed} hideMenu={hideMenu}></SideMenu>
      <Layout className='site-layout'>
        <TopHeader changeState={changeState}></TopHeader>
        <Content>
          <CenterRouter />
        </Content>
      </Layout>
    </Layout>
  )
} 