import React, { useEffect, useState, useRef, useCallback } from "react";
import "./Login.less";
import "./Login.css";
import { validatephone, showDialog } from "../../utils/util";
import { Input, Select, Button, Checkbox, Modal, Tabs } from "antd";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import * as countActions from "@/redux/actions/count";
import { useTranslation } from "react-i18next";
// import type { CheckboxChangeEvent } from "antd/es/checkbox";
import { RedoOutlined, WarningTwoTone } from "@ant-design/icons";
import $ from 'jquery';

import Cookies from "js-cookie";
import CryptoJS from "crypto-js";

import {
  captcha,
  putcaptcha,
  login,
  phoneCode,
  country,
  getCommon,
} from "../../api/common";
const { Option } = Select;



function Login() {
  // 
  // 
  window.captchaCheckCancel = () => {
    // 
    setIsModalVisible(false)
  }
  //  
  window.captchaCheckSuccsss = (bol, captcha, token) => {
    if (bol) {
      localStorage.setItem("jwt", token);
      setincaptcha(captcha);
      if (issubmit) {
        loginpbone(captcha);
      } else {
        setphoneCode(captcha);
      }
      setIsModalVisible(false)
    }
  }

  const { t } = useTranslation();
  const { TabPane } = Tabs;
  const [errortext, seterrortext] = useState("");

  const [forms, validateform] = useState({
    phone: "",
    phone2: "",
    code: "",
    username: "",
    password: "",
    passwordmail: "",
  });
  const [showuser, SetShow] = useState(false);
  const [flag, setFlag] = useState(true);
  let [count, setCountDown] = useState(60);
  const [imgsrc, setimgsrc] = useState("");
  const [spin, setspin] = useState(false);
  const [incaptcha, setincaptcha] = useState("");
  const [caperrortext, setcaperrortext] = useState("");
  const [checked, setchecked] = useState(false);
  const [countrycode, setcountry] = useState(86);
  const [remmber, setremmber] = useState(false);
  const [userName, setuserName] = useState("");
  const [userphone, setuserphone] = useState("");
  const [userPwd, setuserPwd] = useState("");
  const [userPwdemail, setuserPwdemail] = useState("");

  const [options, setoptions] = useState([]);
  const [com, setcom] = useState("");
  const [issubmit, setissubmit] = useState(false);
  const [activeKey, setactiveKey] = useState("1");


  // 
  const [captchaHtml, setCaptchaHtml] = useState(null)



  useEffect(() => {
    getcountry();
    getremmbermes();
    getCommons();
  }, []);
  const getCommons = () => {
    getCommon().then((res) => {
      if (res.data.status === 200) {
        setcom(res.data.data);
      }
      console.log(res, res.data.data, "res");
    });
  };
  const onChangeregister = (e) => {
    setactiveKey(e);
    seterrortext("");
    // validateform({
    //   ...forms,
    //   phone: "",
    // });
    console.log(e, "eee");
  };
  const getremmbermes = () => {
    let username = Cookies.get("userName") ? Cookies.get("userName") : "";
    let userphone = Cookies.get("userphone") ? Cookies.get("userphone") : "";

    let formsobj = JSON.parse(JSON.stringify(forms));
    formsobj.username = username;
    formsobj.phone2 = userphone;
    validateform(formsobj);
    setuserName(username);
    setuserphone(userphone);
    const enPassword = Cookies.get("userPwd") ? Cookies.get("userPwd") : "";
    // 
    let password = CryptoJS.AES.decrypt(enPassword, "user").toString(
      CryptoJS.enc.Utf8
    );
    formsobj.password = password;
    validateform(formsobj);
    setuserPwd(password);
    //
    const userPwdemail = Cookies.get("userPwdemail")
      ? Cookies.get("userPwdemail")
      : "";
    // 
    let Pwdemail = CryptoJS.AES.decrypt(userPwdemail, "user").toString(
      CryptoJS.enc.Utf8
    );
    formsobj.passwordmail = Pwdemail;
    validateform(formsobj);
    setuserPwdemail(Pwdemail);
    // true
    if ((username && Pwdemail) || (userphone && password)) {
      setremmber(true);
    }
  };

  const getcountry = () => {
    country({ keywords: "" }).then((res) => {
      if (res.data.status === 200) {
        setoptions(res.data.data.list);
      }
    });
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    setcaperrortext("");
  };
  const selectcountry = (e) => {
    setcountry(e);
    localStorage.setItem("countrycode", e);
  };
  const onChangechecked = (e) => {
    setchecked(e.target.checked);
    if (e.target.checked) {
      seterrortext("");
    }
    console.log(`checked = ${e.target.checked}`);
  };
  const touserlogin = () => {
    SetShow(!showuser);
    seterrortext("");
    // validateform({
    //   ...forms,
    //   phone: "",
    // });
  };
  const navigate = useNavigate();
  const tocraet = () => {
    navigate("/register");
  };
  const toforget = () => {
    navigate("/forget");
  };

  //
  let timer = null;
  // const [time, setTime] = useState(0);

  const setcode = () => {
    if (!forms.phone) {
      seterrortext("");
      return;
    }
    if (!validatephone(forms.phone)) {
      seterrortext("");
      return;
    }
    console.log(com, "com");
    seterrortext("");
    if (Number(com.captcha_client_login) === 1) {
      setincaptcha("");
      setIsModalVisible(true);
      getcaptchadata();
    } else {
      setphoneCode();
    }
  };

  const setphoneCode = (captcha) => {
    let params = {
      action: "login",
      phone_code: countrycode,
      phone: forms.phone,
      captcha: captcha ? captcha : incaptcha,
      token: localStorage.getItem("jwt"),
    };
    phoneCode(params).then((res) => {
      if (res.data.status === 200) {
        countDown();
      }
    });
  };

  //
  const getcaptchadata = () => {
    captcha().then((res) => {
      // setspin(false);
      if (res.data.status === 200) {
        setCaptchaHtml(res.data.data.html)
        $("#captcha").html(res.data.data.html)
        // setimgsrc(res.data.data.captcha);
        // localStorage.setItem("jwt", res.data.data.token);
      }
      // console.log(res, "res");
    });
  };

  const countDown = () => {
    setFlag(false);
    setCountDown(--count);
    timer = setInterval(() => {
      if (count > 1) {
        setFlag(false);
        setCountDown(--count);
      } else {
        clearInterval(timer);
        timer = null;
        setFlag(true);
        setCountDown(60);
      }
    }, 1000);
  };

  //

  const [isModalVisible, setIsModalVisible] = useState(false);

  const getcaptcha = () => {
    setspin(true);
    getcaptchadata();
  };

  const changecaptcha = (e) => {
    setincaptcha(e.target.value);
    console.log(incaptcha, e.target.value, "incaptcha2");
  };

  const checkcaptcha = () => {
    if (incaptcha) {
      putcaptcha({
        captcha: incaptcha,
        token: localStorage.getItem("jwt"),
      }).then((res) => {
        console.log(res, "ressss");
        if (res.data.status != 200) {
          setcaperrortext(res.data.msg);
        } else {
          setcaperrortext("");
          setIsModalVisible(false);
          if (issubmit) {
            loginpbone();
          } else {
            setphoneCode();
          }
        }
      });
    } else {
      setcaperrortext("");
    }
  };

  //

  const getphonevalue = (e) => {
    validateform({
      ...forms,
      phone: e.target.value,
    });
  };
  const getphonevalue2 = (e) => {
    validateform({
      ...forms,
      phone2: e.target.value,
    });
  };
  const ChangecodeValue = (e) => {
    validateform({
      ...forms,
      code: e.target.value,
    });
  };
  const changeuername = (e) => {
    validateform({
      ...forms,
      username: e.target.value,
    });
  };
  const changepassward = (e) => {
    validateform({
      ...forms,
      password: e.target.value,
    });
  };
  const changepasswardmail = (e) => {
    validateform({
      ...forms,
      passwordmail: e.target.value,
    });
  };
  //
  const encrypt = (str) => {
    const key = CryptoJS.enc.Utf8.parse("idcsmart.finance");
    const iv = CryptoJS.enc.Utf8.parse("9311019310287172");
    var encrypted = CryptoJS.AES.encrypt(str, key, {
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7,
      iv: iv,
    }).toString();
    return encrypted;
  };
  const loginpbone = (captcha) => {
    if (remmber) {
      setpassword();
    } else {
      removepassword();
    }
    seterrortext("");
    let params = {
      type: showuser ? "code" : "password",
      account: showuser
        ? forms.phone
        : !showuser && activeKey === "1"
          ? forms.username
          : !showuser && activeKey === "2"
            ? forms.phone2
            : "",
      phone_code: countrycode,
      code: forms.code,
      password: showuser
        ? forms.phone
        : !showuser && activeKey === "1"
          ? encrypt(forms.passwordmail)
          : !showuser && activeKey === "2"
            ? encrypt(forms.password)
            : "",
      remember_password: remmber ? 1 : 0,
      captcha: captcha ? captcha : incaptcha,
      token: localStorage.getItem("jwt"),
    };
    login(params).then((res) => {
      if (res.data.status === 200) {
        showDialog("", "success");
        navigate("/account");
        localStorage.setItem("jwt", res.data.data.jwt);
      }
    });
  };
  const validateformitem = () => {
    console.log(forms, "forms");
    setissubmit(true);
    getCommons();
    if (showuser) {
      if (!forms.phone) {
        seterrortext("");
        return;
      } else {
        if (!validatephone(forms.phone)) {
          seterrortext("");
          return;
        }
      }
      if (!forms.code) {
        seterrortext("");
        return;
      }
    } else {
      if (activeKey === "1") {
        if (!forms.username) {
          seterrortext("");
          return;
        }
        if (
          forms.username.search(
            /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/
          ) === -1
        ) {
          seterrortext("");
          return;
        }
        if (!forms.passwordmail) {
          seterrortext("");
          return;
        }
      } else {
        if (!forms.phone2) {
          seterrortext("");
          return;
        }
        if (!validatephone(forms.phone2)) {
          seterrortext("");
          return;
        }
        if (!forms.password) {
          seterrortext("");
          return;
        }
      }
    }
    console.log(checked, "checked");
    if (!checked) {
      seterrortext("");
      return;
    }
    seterrortext("");
    if (showuser) {
      loginpbone();
    } else if (
      !showuser &&
      ((Number(com.captcha_client_login) === 1 &&
        Number(com.captcha_client_login_error) === 0) ||
        (Number(com.captcha_client_login_error_3_times) === 1 &&
          Number(com.captcha_client_login) === 1 &&
          Number(com.captcha_client_login_error) === 1))
    ) {
      setincaptcha("");
      console.log(incaptcha, "111");
      setIsModalVisible(true);
      getcaptchadata();
    } else {
      loginpbone();
    }
  };
  //
  const setpassword = () => {
    Cookies.set("userName", forms.username, {
      expires: 30,
    });
    Cookies.set("userphone", forms.phone2, {
      expires: 30,
    });
    Cookies.set("userPwd", CryptoJS.AES.encrypt(forms.password, "user"), {
      expires: 30, // 30
    });
    Cookies.set(
      "userPwdemail",
      CryptoJS.AES.encrypt(forms.passwordmail, "user"),
      {
        expires: 30, // 30
      }
    );
  };
  const removepassword = () => {
    // cookie
    Cookies.remove("userName");
    Cookies.remove("userPwd");
    Cookies.remove("userphone");
    Cookies.remove("userPwdemail");
  };
  const remmberpassword = (e) => {
    setremmber(e.target.checked);
    // if (e.target.checked) {
    //   setpassword();
    // } else {
    //   removepassword();
    // }
    console.log(forms.phone, "forms.phone");
  };
  return (
    <div className="login">
      <div className="l-bg"></div>
      <div className="r-bg"></div>
      <div className="r-bg-color"></div>
      <div className="form login_form">
        <div className="login_title">{t("login.login_title")}</div>
        <div className="login_subtitle">
          <span>{t("login.no_account")}</span>
          <span className="toregister" onClick={tocraet}>
            {t("login.creat_account")}
          </span>
        </div>
        <div>
          {showuser ? (
            <div>
              <div className="form_item select_input codephoneinput">
                <Select
                  defaultValue=" +86"
                  className="item_select"
                  onChange={selectcountry}
                >
                  {options.map((item, index) => {
                    return (
                      <Option
                        key={index}
                        value={item.phone_code}
                        title={item.name_zh + item.phone_code}
                      >
                        {item.name_zh}+{item.phone_code}
                      </Option>
                    );
                  })}
                </Select>
                <Input
                  style={{
                    width: "100%",
                    backgroundColor: "#f3f3f9",
                  }}
                  bordered={false}
                  onChange={getphonevalue}
                />
              </div>
              <div className="code_item">
                <div className="form_item_code">
                  <Input
                    style={{
                      width: "100%",
                      backgroundColor: "#f3f3f9",
                    }}
                    bordered={false}
                    placeholder={t("login.plcode")}
                    onChange={ChangecodeValue}
                  />
                </div>
                {flag ? (
                  <Button className="bottom_code" onClick={setcode}>
                    {t("login.code")}
                  </Button>
                ) : (
                  <div className="count_down">
                    {count} {t("login.count")}
                  </div>
                )}
                <div className="setcodebox">
                  {errortext ? (
                    <div className="flex just_between">
                      <div className="errortip">
                        <WarningTwoTone twoToneColor="#f64e60" />
                        {errortext}
                      </div>
                    </div>
                  ) : (
                    ""
                  )}
                </div>
              </div>
            </div>
          ) : (
            <div>
              <Tabs
                defaultActiveKey="1"
                centered="true"
                onChange={onChangeregister}
              >
                <TabPane tab={t("login.email")} key="1">
                  <div className="form_item username">
                    <Input
                      style={{
                        width: "100%",
                        backgroundColor: "#f3f3f9",
                      }}
                      bordered={false}
                      placeholder={t("login.email")}
                      defaultValue={userName}
                      onChange={changeuername}
                    />
                  </div>
                  <div className="form_item passward">
                    <Input
                      style={{
                        width: "100%",
                        backgroundColor: "#f3f3f9",
                      }}
                      bordered={false}
                      placeholder=""
                      defaultValue={userPwdemail}
                      type="password"
                      onChange={changepasswardmail}
                    />
                    {errortext ? (
                      <div className="errortip pass_tip">
                        <WarningTwoTone twoToneColor="#f64e60" />
                        {errortext}
                      </div>
                    ) : (
                      ""
                    )}
                  </div>
                </TabPane>
                <TabPane tab={t("login.phone")} key="2">
                  <div className="form_item select_input">
                    <Select
                      defaultValue=" +86"
                      className="item_select"
                      onChange={selectcountry}
                    >
                      {options.map((item, index) => {
                        return (
                          <Option key={index} value={item.phone_code}>
                            {item.name_zh}+{item.phone_code}
                          </Option>
                        );
                      })}
                    </Select>
                    <Input
                      style={{
                        width: "70%",
                        backgroundColor: "#f3f3f9",
                      }}
                      bordered={false}
                      defaultValue={userphone}
                      onChange={getphonevalue2}
                    />
                  </div>
                  <div className="form_item passward">
                    <Input
                      style={{
                        width: "100%",
                        backgroundColor: "#f3f3f9",
                      }}
                      bordered={false}
                      placeholder=""
                      defaultValue={userPwd}
                      type="password"
                      onChange={changepassward}
                    />
                    {errortext ? (
                      <div className="errortip pass_tip">
                        <WarningTwoTone twoToneColor="#f64e60" />
                        {errortext}
                      </div>
                    ) : (
                      ""
                    )}
                  </div>
                </TabPane>
              </Tabs>

              <div className="forget flex just_between">
                <Checkbox onChange={remmberpassword} checked={remmber}>
                  {t("login.remmber")}
                </Checkbox>
                <div className="forgettext" onClick={toforget}>
                  {t("login.forget")}
                </div>
              </div>
            </div>
          )}
          <div className="login_btn phonelogin_btn" onClick={validateformitem}>
            {t("login.login")}
          </div>
          <div className="agreement">
            <Checkbox onChange={onChangechecked}>
              {t("login.agreement")}
              <a className="agreement_link" href={com.terms_service_url}>
                {t("login.link")}
              </a>
            </Checkbox>
          </div>
          <div className="login_btn userlogin_btn" onClick={touserlogin}>
            {showuser ? t("login.userlogin_btn") : t("login.code_login")}
          </div>
        </div>
      </div>


      {
        isModalVisible &&
        <div id="captcha" >

        </div>
      }
      {/* <Modal
        title={t("login.captchatit")}
        visible={isModalVisible}
        maskClosable
        className="picCheck"
        onCancel={handleCancel}
        footer={[]}
      >
        <div className="captchabox flex just_between">
          <Input
            style={{
              width: "70%",
              backgroundColor: "#f3f3f9",
              height: "40px",
            }}
            bordered={false}
            onChange={changecaptcha}
            defaultValue=""
            value={incaptcha}
          />
          <img src={imgsrc} alt="" className="captcha" />
          <RedoOutlined
            rotate={290}
            // spin={spin}
            className="outlined"
            onClick={getcaptcha}
          />
          <div className="secaptchabox">
            {caperrortext !== "" ? (
              <div className="flex just_between">
                <div className="errortip">
                  <WarningTwoTone twoToneColor="#f64e60" />
                  {caperrortext}
                </div>
              </div>
            ) : (
              ""
            )}
          </div>
        </div>
        <div className="check_btn" onClick={checkcaptcha}>
          {t("login.captchabtn")}
        </div>

      </Modal> */}
    </div>
  );
}
// 
export default connect((state) => ({}))(Login);
