/*  */
import request from "../utils/request";

// 
export function getCommon() {
  return request.get(`/common`);
}
// 
export function country(param) {
  return request.get(`/country`, { params: { ...param } });
}
// 
export function phoneCode(params) {
  return request.post(`/phone/code`, params);
}
// 
export function emailCode(params) {
  return request.post(`/email/code`, params);
}
// 
export function captcha() {
  return request.get(`/captcha`);
}
// 
export function putcaptcha(params) {
  return request.post(`/captcha`, params);
}
// 
export function login(params) {
  return request.post(`/login`, params);
}
export function register(params) {
  return request.post(`/register`, params);
}
export function passwordReset(params) {
  return request.post(`/account/password_reset`, params);
}
export function getUserId() {
  return request.get("/get_client_id");
}
export function upoadFile(params) {
  return request.post("/upload", params);
}
// 
export function logout() {
  return request.post(`/logout`);
}

//
export function globalSearch(param) {
  return request.get("/global_search", { params: { ...param } });
}
