import React, { useState, useEffect } from "react";
import { Layout, Menu, Tag } from "antd";
import { useTranslation } from "react-i18next";
import { NavLink, useLocation } from "react-router-dom";
import { MailOutlined } from "@ant-design/icons";
import "./SideMenu.css";
export default function SideMenu (props) {
  const { t } = useTranslation();
  const { Sider } = Layout;
  const [current, setCurrent] = useState("account");
  const curPath = useLocation().pathname.split("/")[1];
  useEffect(() => {
    setCurrent(curPath);
  }, []);
  function hideMenu () {
    props.hideMenu();
  }
  const checkMenu = (e) => {
    setCurrent(e.key);
  };
  const items = [
    {
      label: t("menu.product"),
      key: "product",
      children: [
        {
          icon: <img src={require(`@/assets/images/menu/${current === 'cloud_list' ? 'c-' : ''}cloud.png`)} alt="" />,
          label: (
            <NavLink to="/cloud_list">
              {t("cloud.instance")}&nbsp;&nbsp;<Tag color="#F5A953">{t("menu.discount")}</Tag>
            </NavLink>
          ),
          key: "cloud_list",
        },
        {
          icon: <img src={require(`@/assets/images/menu/${current === 'network' ? 'c-' : ''}network.png`)} alt="" />,
          label: (
            <NavLink to="/network">
              {t("cloud.network")}
            </NavLink>
          ),
          key: "network",
        },
        {
          icon: <img src={require(`@/assets/images/menu/${current === 'storage' ? 'c-' : ''}storage.png`)} alt="" />,
          label: (
            <NavLink to="/storage">
              {t("menu.storage")}
            </NavLink>
          ),
          key: "storage",
        },
        // {
        //   icon: <img src={require(`@/assets/images/menu/${current === 'monitor' ? 'c-' : ''}monitor.png`)} alt="" />,
        //   label: (
        //     <NavLink to="/monitor">
        //       {t("menu.monitor")}
        //     </NavLink>
        //   ),
        //   key: "monitor",
        // },
        {
          icon: <img src={require(`@/assets/images/menu/${current === 'image' ? 'c-' : ''}image.png`)} alt="" />,
          label: (
            <NavLink to="/image">
              {t("menu.image")}
            </NavLink>
          ),
          key: "image",
        },
      ],
    },
    {
      icon: <img src={require(`@/assets/images/menu/${current === 'account' ? 'c-' : ''}account.png`)} alt="" />,
      label: (
        <NavLink to="/account" key="account">
          {t("menu.account")}
        </NavLink>
      ),
      key: "account",
    },
    {
      icon: <img src={require(`@/assets/images/menu/${current === 'support' ? 'c-' : ''}support.png`)} alt="" />,
      label: (
        <NavLink to="/support" key="support">
          {t("menu.support")}
        </NavLink>
      ),
      key: "support",
    },
    {
      icon: <img src={require(`@/assets/images/menu/${current === 'finance' ? 'c-' : ''}finance.png`)} alt="" />,
      label: (
        <NavLink to="/finance" key="finance">
          {t("menu.finance")}
        </NavLink>
      ),
      key: "finance",
    },
    {
      icon: <img src={require(`@/assets/images/menu/${current === 'promote' ? 'c-' : ''}promote.png`)} alt="" />,
      label: (
        <NavLink to="/promote" key="account">
          {t("menu.promote")}
        </NavLink>
      ),
      key: "promote",
    },
  ];
  return (
    <Sider
      collapsedWidth="0"
      trigger={null}
      collapsible
      collapsed={props.collapsed}
      className="com-sider"
      width={265}
    >
      <div className="logo">
        <img src={require(`@/assets/images/logo.png`)} alt="" />
      </div>
      <Menu
        mode="inline"
        theme="dark"
        defaultOpenKeys={["product"]}
        selectedKeys={[current]}
        items={items}
        onClick={checkMenu}
      />
      <div
        className={`com-mask ${!props.collapsed ? "hide" : ""}`}
        onClick={hideMenu}
      ></div>
    </Sider>
  );
}
