const changeRoutes = (routes) => {
  return routes.map((item) => {
    if (item.children) {
      item.children = changeRoutes(item.children);
    }
    /* <Loading /> */
    item.element = <Suspense fallback={null}>{item.element}</Suspense>;
    return item;
  });
};

const CenterRouter = () => useRoutes(changeRoutes(routes));
export { routes, CenterRouter };
